@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-home {
  background-image: url(/static/media/mvp-banner.e1143cc0.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 50rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.text-content {
  display: flex;
  justify-content: center;
}
.button {
  display: flex;
  justify-content: center;
}
#order-pizza {
  padding: 1rem 3rem 1rem 3rem;
  background-color: #fdc913;
  border-radius: 2rem;
  font-weight: bold;
  border: none;
}

.container-home p {
  text-align: center;
  font-size: 3rem;
  color: aliceblue;
  font-family: "Barlow", sans-serif;
}

.nav-header {
  background-color: #ce2829;
  display: flex;
  justify-content: center;
  padding: 3rem;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.content-1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40rem;
  padding: 2rem;
}
.boyut input {
  margin: 0.6rem;
}
.radio-form {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.checkbox-1 {
  display: grid;
  grid-template-columns: repeat(3, 10rem);
  grid-gap: 10px;
}
.checkbox-1 input {
  margin: 0.3rem;
}

.ek-malzemeler {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.boyut {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: 5rem;
}
.hamur {
  margin-left: 8rem;
}

.header {
  display: flex;
  flex-direction: column;
  padding-bottom: 2rem;
}

.nav-bar {
  background-color: #ce2829;
  padding: 0.1rem;
}
.nav-bar ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding-left: 0px;
}
.nav-bar li {
  padding-left: 0.2rem;
  padding-right: 0.2rem;
  color: aliceblue;
}

.nav-bar ul li a {
  color: white;
  text-decoration: none;
}

#order-button {
  width: 100%;
  background-color: #fdc913;
  border: none;
  outline: none;
  padding: 20px;
  font-weight: bold;
  border-radius: 8px;
  margin-top: 2rem;
  color: black;
}
hr {
  border: none;
  border-top: 2px solid black;
}
.bottom {
  display: flex;
  flex-direction: row;
}
.pizza-adet {
  display: flex;
  flex-direction: row;
  height: 3rem;
  width: 10rem;
}
.adet-sayisi {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  border: 1px solid gainsboro;
  border-radius: 5px;
}
.adet-sayisi p {
  display: flex;
  flex-direction: row;
  text-align: center;
  margin-bottom: 0;
}
.siparis-detayi {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid gainsboro;
  font-weight: bold;
  font-family: "Barlow", sans-serif;
  width: 22rem;
  margin-left: 4rem;
  border-radius: 8px;
}

.arti {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  border: 1px solid gainsboro;
  border-radius: 5px;
  background-color: #ffc107;
}
.arti p {
  text-align: center;
  margin-bottom: 0;
  cursor: pointer;
  outline: none;
}
.arti:hover {
  background-color: #ffe186;
}
.eksi {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  border: 1px solid gainsboro;
  border-radius: 5px;
  background-color: #ffc107;
}
.eksi p {
  text-align: center;
  margin-bottom: 0;
  cursor: pointer;
  outline: none;
}
.eksi:hover {
  background-color: #ffe186;
}
.siparis-toplami {
  margin: 2rem;
}

.success {
  background-color: #ce2829;
  background-image: url(/static/media/mvp-banner.e1143cc0.png);
  width: 100%;
  height: 50rem;
  color: aliceblue;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.success p {
  font-size: 3rem;
}

